import axios from "axios";

let config = require("./config.json");

const pathUrl = process.env.REACT_APP_API_URL;
export const ApiService = {
    get: (url, params, configLocal) => {
        const getToken = configLocal ? configLocal.token || localStorage.getItem("token") : localStorage.getItem("token");
        // console.log("line 9", pathUrl);
        const localConfigAxios = {
            headers: {
                'Authorization': `Bearer ${getToken}`,
            },
            params,
            ...configLocal
        }
        return axios.get(pathUrl + url, localConfigAxios)
    },

    post: (url, params, configLocal) => {
        const getToken = configLocal ? configLocal.token || localStorage.getItem("token") : localStorage.getItem("token");
        console.log("line 9", pathUrl);
        const localConfigAxios = {
            headers: {
                'Authorization': `Bearer ${getToken}`,
            },
            ...configLocal
        }

        return axios.post(pathUrl + url, params, localConfigAxios)
    },

}